
html {
  background: #eeeeee;
}
html, body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: 'Roboto', sans-serif;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
html .wechat-list,
.wechat .recyclerview {
  background: #efefef;
}
.switch {
  position: fixed;
  top: 50px;
  right: 0;
  z-index: 100;
  background: #03A9F4;
  color: #fff;
  font-size: 14px;
  padding: 10px;
}
.nav-header {
  background: #373b3e;
  height: 50px;
  line-height: 50px;
  color: #fff;
  padding: 0 20px;
}
.nav-header-action {
  position: absolute;
  height: 50px;
  width: 50px;
  padding-right: 10px;
  top: 0;
  right: 0;
  color: #fff;
  line-height: 45px;
  text-align: right;
}
.nav-header-action-icon {
  width: 26px;
  margin-top: 14px;
}
.recyclerview-container {
  height: calc(100vh - 50px);
}
.testlist {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.chat-view-footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 50px;
}
.wechat {
  height: calc(100vh - 100px);
}
.chat-input {
  width: 100%;
  height: 50px;
  border: none;
  outline: none;
  padding: 5px;
  line-height: 14px;
}
.chat-send-btn {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  width: 100px;
  height: 50px;
  background: #8BC34A;
  border: none;
  color: #fff;
  font-size: 14px;
}



